import { computed, makeAutoObservable } from 'mobx';

export interface Imessage {
    id: number;
    text: string;
    time: string; // 8:30
    isReaded: boolean;
}

export interface Iuser {
    id: number;
    name: string;
    isTyping: boolean;
    online: boolean;
    avatar: string;
    unreadMessagesCount: number;
    messages: Imessage[];
}

